<template>
    <input type="checkbox" :value="value" v-model="proxyChecked"
           class="rounded border-light-300 dark:border-dark-300 text-blue shadow-sm focus:border-blue focus:ring focus:ring-blue focus:ring-opacity-50">
</template>

<script>
export default {
    emits: ['update:checked'],

    props: {
        checked: {
            type: [Array, Boolean],
            default: false,
        },
        value: {
            default: null,
        },
    },

    computed: {
        proxyChecked: {
            get() {
                return this.checked;
            },

            set(val) {
                this.$emit("update:checked", val);
            },
        },
    },
}
</script>
